<template>
  <div class="complaint-management">
    <!--  -->
    <div class="title-bar">
      <div class="title"></div>
    </div>
    <!--  -->
    <el-row class="search-row">
      <el-col :span="7">
        <el-input v-model="search.dept" placeholder=""></el-input>
      </el-col>
      <el-col :span="7">
        <el-date-picker v-model="search.date" type="datetimerange" placeholder=""></el-date-picker>
      </el-col>

      <el-col :span="4" >
        <el-form-item>
          <el-select v-model.trim="search.state" clearable placeholder="" >
            <el-option
                v-for="item in AcceptedOrNot"
                :key="item.value"
                :label="item.label"
                :value="item.value"
            />
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="1">
        <el-button type="primary" @click="searchComplaintments"></el-button>
      </el-col>
    </el-row>

    <!--  -->
    <el-table :data="complaints" style="width: 100%">
      <el-table-column label="" prop="title"></el-table-column>
      <el-table-column label="" prop="toComplaintDept"></el-table-column>
      <el-table-column label="" prop="toComplaintName"></el-table-column>
      <el-table-column label="" prop="time"></el-table-column>
      <el-table-column label="" prop="state">
        <template #default="{ row }">
          {{ statusMappings[row.state] }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template #default="{ row }">
          <el-button type="text" @click="viewComplaint(row)">{{ row.state === 0? '' : '' }}</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
  <el-dialog title="" v-model="complaintDialogVisible">
    <div>
      <p style="font-size: 20px;"><strong></strong>{{ selectedComplaint.title }}</p>
      <p style="font-size: 18px;"><strong></strong>{{ selectedComplaint.content }}</p>

      <el-form ref="replyForm" :model="replyForm" label-width="80px">
        <el-form-item label="">
          <el-input type="textarea" v-model="replyForm.replyContent" rows="3"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="replyForm.time" type="date" placeholder=""></el-date-picker>
        </el-form-item>
      </el-form>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="cancelComplaint"></el-button>
      <el-button type="primary" @click="completeComplaint"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import axios from "axios";
import ServicePort from './ServicePort';
export default {
  name:'complaint',
  data() {
    return {
      statusMappings: {
        '0': '',
        '1': '',
        '2': '',
      },
      searchTitle: '',
      searchDate: '',
      search:{
        dept: '',
        date: '',
        state:'',
      },
      selectedComplaint: {
        title: '',
        content: '',
      },
      currentEditComplaint: null,
      replyForm: {
        replyContent: ''
      },
      complaintDialogVisible: false,
      complaints: [
      ],
      complaintForm: { // 
        complaintNumber: '',
        complaintTitle: '',
        complaintContent: '',
        complaintDate:'',
        complaintState:'',
        // 
      },
      AcceptedOrNot:[
        {
          value: '1',
          label: '',
        },
        {
          value: '0',
          label: '',
        },
      ],
    };
  },
  mounted() {
    this.fetchComplaint();
  },
  methods: {
    async fetchComplaint() {
      try {
        const searchParams = {
          toComplaintDept: this.search.dept,
          startDate: this.search.date[0], // 
          endDate: this.search.date[1],   // 
          state: this.search.state,
        };
        const complaints = await ServicePort.fetchComplaint(searchParams);
        this.complaints = complaints;
      } catch (error) {
        // 
      }
    },
    searchComplaintments() {
      this.fetchComplaint();
    },
    viewComplaint(row) {
      this.currentEditComplaint = row;
      // 
      this.selectedComplaint.title = row.title;
      this.selectedComplaint.content = row.content;
      this.selectedComplaint.complaintId = row.complaintId
      // 
      this.complaintDialogVisible = true;
    },
    cancelComplaint() {
      this.replyForm.content = '';
      this.complaintDialogVisible = false;
    },
    async completeComplaint(){
      try {
        const complaintData = {
          content: this.replyForm.replyContent,
          complaintId:this.selectedComplaint.complaintId,
          replyerId:this.$store.getters.id,
          time:this.replyForm.time,
        };
        console.log(complaintData);

        await axios.post(`http://localhost:80/complaint/reply`, complaintData);

        // 
        this.replyForm.replyContent = '';
        this.replyForm.time = '';
        alert('');
        // 
        this.complaintDialogVisible = false;
        // 
        this.fetchComplaint();
      } catch (error) {
        // 
      }
    },
  }
};
</script>

<style scoped>
.complaint-management {
  padding: 0;
}

.title {
  color: #000;
  font-size: 24px;
}

.search-row {
  margin-top: 10px;
  margin-bottom: 10px;
}
.title-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #f5f5f5;
}
</style>
