<template>
  <div>
    <el-form ref="modelForm" :rules="rules" :model="modelForm" label-position="right" label-width="100px">
      <el-collapse>
        <el-collapse-item v-for="(item, index) in modelForm.topic" :key="index">
          <template #title>
            <span>{{ index+1 }},:{{ item.questionName }}</span>
          </template>
          <!--  -->
          <el-form-item :prop="`topic.${index}.type`" :label="`${index + 1}`" :rules="[{ required: true, message: '', trigger: 'change' }]">
            <el-radio-group v-model="item.type">
              <el-radio :label="0"></el-radio>
              <el-radio :label="1"></el-radio>
            </el-radio-group>
          </el-form-item>
          <!--  -->
          <el-form-item :prop="`topic.${index}.questionName`" label="" :rules="[{ required: true, message: '', trigger: 'change' }]">
            <el-input v-model.trim="item.questionName" style="width:258px" clearable placeholder="" />
          </el-form-item>
          <!--  -->
          <el-form-item v-for="(opt, idx) in item.answers" v-show="item.type !== 2" :key="idx" :label="`${idx + 1}`" :prop="`topic.${index}.answers.${idx}.value`" :rules="[{ required: true, message: '', trigger: 'blur' }]">
            <el-input v-model.trim="opt.value" style="width:258px" clearable placeholder="" />
            <el-button style="margin-left: 20px" @click.prevent="removeDomain(index, idx)"></el-button>
          </el-form-item>
          <el-form-item>
            <el-button v-show="item.type !== 2" @click="addDomain(index)"></el-button>
            <el-button @click="removeQuestion(index)"></el-button>
          </el-form-item>
        </el-collapse-item>
      </el-collapse>

      <el-form-item>
        <el-button @click="addQuestion"></el-button>
        <el-button @click="addSubmit()"></el-button>
        <el-button @click="resetForm('modelForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>

export default {
  name: 'HelloWorld',
  data() {
    return {
      rules: {},
      modelForm: {
        topic: [{
          type: '',
          questionName: '',
          answers: [{ value: '' }]
        }, {
          type: '',
          questionName: '',
          answers: [{ value: '' }]
        }]
      }
    }
  },
  methods: {
    end(evt) {
      this.$refs.modelForm.clearValidate()
    },
    removeDomain(index, idx) { // 
      this.modelForm.topic[index].answers.splice(idx, 1)
    },
    removeQuestion(index) {//
      this.modelForm.topic.splice(index, 1)
    },
    addDomain(index) { // 
      this.modelForm.topic[index].answers.push({ value: '' })
    },
    addQuestion() { // 
      this.modelForm.topic.push({ type: '', questionName: '', answers: [{ value: '' }] })
    },
    resetForm(formName) { // 
      this.$refs[formName].resetFields()
    },
    addSubmit() {
      this.$refs.modelForm.validate(valid => {
        if (valid) {
          console.log(this.modelForm.topic)
        }
      })
    }
  }
}
</script>

