<template>
  <div class="consult-management">
    <!--  -->
    <div class="title-bar">
      <div class="title"></div>
    </div>
    <!--  -->
    <el-row class="search-row">
      <el-col :span="7">
        <el-input v-model="search.title" placeholder=""></el-input>
      </el-col>
      <el-col :span="7">
        <el-date-picker v-model="search.time" type="datetimerange" placeholder=""></el-date-picker>
      </el-col>
      <el-col :span="4" >
        <el-form-item>
          <el-select v-model.trim="search.state" clearable placeholder="" >
            <el-option
                v-for="item in AcceptedOrNot"
                :key="item.value"
                :label="item.label"
                :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="1">
        <el-button type="primary"  @click.native="searchConsultations" ></el-button>
      </el-col>

    </el-row>

    <!--  -->
    <el-table :data="consults" style="width: 100%">
      <el-table-column label="" prop="title"></el-table-column>
      <el-table-column label="" prop="company"></el-table-column>
      <el-table-column label="" prop="name"></el-table-column>
      <el-table-column label="" prop="mobile"></el-table-column>
      <el-table-column label="" prop="time" ></el-table-column>
      <el-table-column label="" prop="state">
        <template #default="{ row }">
          {{ statusMappings[row.state] }}
        </template>
      </el-table-column>
      <el-table-column label="">
        <template #default="{ row }">
          <el-button type="text" @click="viewConsult(row)">{{ row.state === 0? '' : '' }}</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
  <el-dialog title="" v-model="consultDialogVisible">
    <div>
      <p style="font-size: 20px;"><strong></strong>{{ selectedConsult.title }}</p>
      <p style="font-size: 18px;"><strong></strong>{{ selectedConsult.content }}</p>

      <el-form ref="replyForm" :model="replyForm" label-width="80px">
        <el-form-item label="">
          <el-input type="textarea" v-model="replyForm.replyContent" rows="3"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker v-model="replyForm.time" type="date" placeholder=""></el-date-picker>
        </el-form-item>
      </el-form>
    </div>

    <div slot="footer" class="dialog-footer">
      <el-button @click="cancelConsult"></el-button>
      <el-button type="primary" @click="completeConsult"></el-button>
    </div>
  </el-dialog>
</template>

<script>
import axios from "axios";
import ServicePort from './ServicePort';
export default {
  name:'consult',
  data() {
    return {
      // searchTitle: '',
      // searchDate: '',
      // searchState:'',
      consults: [],
      statusMappings: {
        '0': '',
        '1': '',
        '2': '',
      },
      search: {
        title: '',//
        time: '', // 
        state:'',
      },
      selectedConsult: {
        title: '',
        content: '',
      },
      currentEditConsult: null,
      replyForm: {
        replyContent: ''
      },
      consultDialogVisible: false,
      consultForm: { // 
        consultNumber: '',
        consultTitle: '',
        consultContent: '',
        consultDate:'',
        consultState:'',
        // 
      },
      AcceptedOrNot:[
        {
          value: '1',
          label: '',
        },
        {
          value: '0',
          label: '',
        },
      ],
    };
  },
  mounted() {
    this.fetchConsultations();
  },
  methods: {

    async fetchConsultations() {
      try {
        const searchParams = {
          title: this.search.title,
          startDate: this.search.time[0], // 
          endDate: this.search.time[1],   // 
          state: this.search.state,
        };
        const consultations = await ServicePort.fetchConsultations(searchParams);
        this.consults = consultations;
      } catch (error) {
        // 
      }
    },
    searchConsultations() {
      this.fetchConsultations();
    },

    viewConsult(row) {
      this.currentEditConsult = row;
      // 
      this.selectedConsult.title = row.title;
      this.selectedConsult.content = row.content;
      this.selectedConsult.consultationId = row.consultationId
      // 
      this.consultDialogVisible = true;
    },
    cancelConsult() {
      this.replyForm.content = '';
      this.consultDialogVisible = false;
    },
    async completeConsult(){
      try {
        const consultData = {
          content: this.replyForm.replyContent,
          consultationId:this.selectedConsult.consultationId,
          replyerId:this.$store.getters.id,
          time:this.replyForm.time,
        };
        console.log(consultData);
        // 
        await axios.post(`http://localhost:80/consultation/reply`, consultData);

        // 
        this.replyForm.replyContent = '';
        this.replyForm.time = '';
        alert('');
        // 
        this.consultDialogVisible = false;
        // 
        this.fetchConsultations();
      } catch (error) {
        // 
      }
    },
  }
};
</script>

<style scoped>
.consult-management {
  padding: 0;
}

.title {
  color: #000;
  font-size: 24px;
}

.search-row {
  margin-top: 10px;
  margin-bottom: 10px;
}
.title-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #f5f5f5;
}
</style>
